import SIDEBAR from "./componentss/sidebar";
import Row from "react-bootstrap/Row";
import Col from "react-bootstrap/Col";
import Header1 from "./componentss/Header1";
import React, { useEffect, useState } from "react";
import { useAuth } from "./AuthContextAdmin";
import { useNavigate, Link } from "react-router-dom";
import { ToastContainer, toast } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";
import axios from "axios";
import Table from "react-bootstrap/Table";
import Container from "react-bootstrap/Container";
import { Button } from "react-bootstrap";
import Spinner from "react-bootstrap/Spinner";
const OrderList = () => {
  const [orders, setOrders] = useState([]);

  useEffect(() => {
    // Fetch the list of orders when the component mounts
    fetchOrders();
  }, []);

  const fetchOrders = async () => {
    try {
      const response = await axios.get(
        "https://backend-do-an-chuyen-nganh.vercel.app/v1/api/admin/api/orders"
      );
      setOrders(response.data);
    } catch (error) {
      console.error("Error fetching orders:", error);
    }
  };
  const { adminToken } = useAuth();
  const navigate = useNavigate();

  useEffect(() => {
    // Kim tra nu ngi dng cha ng nhp, chuyn hng v trang login
    if (!adminToken) {
      navigate("/TourDuLichTravel/admin");
    }
  }, [adminToken, navigate]);

  if (!adminToken) {
    // Nu cha ng nhp, khng hin th ni dung ca trang
    return null;
  }
  const formatCurrency = (price) => {
    // Chuyn i gi tr DECIMAL(10) sang s nguyn
    const priceInteger = Math.round(price * 100); // Gi s 2 ch s thp phn

    // S dng hm toLocaleString  nh dng gi theo kiu VN
    const formattedPrice = (priceInteger / 100).toLocaleString("vi-VN", {
      currency: "VND",
    });

    // Thm ch "VN" vo cui chui
    return formattedPrice;
  };
  const getStatusColor = (status) => {
    switch (status) {
      case "Tip nhn":
        return "yellow";
      case " thanh ton":
        return "#99FF99"; // Mu cam cho trng thi 'Pending'
      // Mu xanh cho trng thi 'Confirmed'
      case " hu":
        return "#FF6633";
      case " kt thc":
        return "#FF9900";
      // Mu  cho trng thi 'Cancelled'
      default:
        return "black"; // Mu mc nh nu trng thi khng khp vi bt k trng hp no
    }
  };

  return (
    <>
      <ToastContainer />
      <Header1 />
      <div className="boxGioiThieu"></div>
      <Row className="d-flex flex-row">
        <Col className="col-2">
          <SIDEBAR />
        </Col>
        <Col className="col-10 ">
          <div className="ms-5 ps-3 mt-3">
            <h2>Order List</h2>
            <Table striped bordered hover>
              <thead>
                <tr>
                  <th>M Booking</th>
                  <th>Tn Tour</th>
                  <th>Thng tin KH</th>

                  <th>D liu tour</th>
                  <th>Thi gian t</th>
                  <th>Trng thi</th>
                  <th>Chi tit</th>
                </tr>
              </thead>
              <tbody>
                {orders.map((order) => (
                  <tr key={order.OrderID}>
                    <td>{order.id}</td>
                    <td>{order.name}</td>
                    <td className="lh-lg">
                      <span className="fw-bold">Tn: </span>
                      {order.fullname}
                      <br />
                      <span className="fw-bold">Email: </span>
                      {order.email}
                      <br />

                      <span className="fw-bold">Phone: </span>
                      {order.phone}
                      <br />

                      <span className="fw-bold">a ch: </span>
                      {order.address}
                      <br />
                    </td>

                    <td className="lh-lg">
                      <span className="fw-bold">Ngy bt u: </span>
                      <span className="text-primary fw-bold">
                        {new Date(order.start_date).toLocaleDateString()}
                      </span>
                      <br />
                      <span className="fw-bold">Ngy kt thc: </span>
                      <span className="text-primary fw-bold">
                        {new Date(order.end_date).toLocaleDateString()}
                      </span>
                      <br />
                      <span className="fw-bold">S ngi ln: </span>
                      {order.quantity}
                      <br />
                      <span className="fw-bold">S tr em: </span>
                      {order.child_quantity}
                      <br />
                      <span className="fw-bold">S tr nh: </span>
                      {order.infant_quantity}
                      <br />
                      <span className="fw-bold">Tng tin: </span>
                      <span className="text-danger fw-bold">
                        {formatCurrency(order.total_price)}
                      </span>

                      <br />
                    </td>
                    <td>
                      {new Date(order.booking_date_time).toLocaleDateString(
                        "vi-VN",
                        {
                          year: "numeric",
                          month: "numeric",
                          day: "numeric",
                          hour: "numeric",
                          minute: "numeric",
                          second: "numeric",
                        }
                      )}
                    </td>
                    <td
                      style={{
                        backgroundColor: getStatusColor(order.status),
                      }}
                    >
                      {order.status}
                    </td>
                    <td>
                      <Link
                        to={`/TourDuLichTravel/admin/OrderList/${order.id}`}
                      >
                        <Button className="col-12"> Chi tit</Button>
                      </Link>
                    </td>
                  </tr>
                ))}
              </tbody>
            </Table>
          </div>
        </Col>
      </Row>
    </>
  );
};
export default OrderList;
