import Row from "react-bootstrap/Row";
import Col from "react-bootstrap/Col";
import Container from "react-bootstrap/Container";
import Table from "react-bootstrap/Table";
import "../assets/scss/DetailTourMienTay3Ngay2Dem.scss";
import LocalOfferIcon from "@mui/icons-material/LocalOffer";
import InfoIcon from "@mui/icons-material/Info";
import Form from "react-bootstrap/Form";
import Button from "react-bootstrap/Button";
import React, { useState, useEffect } from "react";
import { Link } from "react-router-dom";
import Header from "../components/Header";
import BackToTop from "../components/BackToTop";
import axios from "axios";
import { useParams, useNavigate } from "react-router-dom";
import { useAuth } from "./AuthContext";

import BookingForm from "../components/BookingForm";

const Bookingtour = () => {
  const [validated, setValidated] = useState(false);
  const { userId } = useAuth();

  const { tourID, userID } = useParams();

  const [tour, setTour] = useState([]);
  const [images, setImages] = useState([]);
  useEffect(() => {
    const fetchTour = async () => {
      try {
        const responseTour = await axios.get(
          `https://backend-do-an-chuyen-nganh.vercel.app/v1/api/admin/lay-thong-tin-tour/${tourID}`
        );
        const responseImages = await axios.get(
          `https://backend-do-an-chuyen-nganh.vercel.app/v1/api/admin/lay-hinh-anh-tour/${tourID}`
        );

        setTour(responseTour.data);
        setImages(responseImages.data);
      } catch (error) {
        console.error("Li khi ly thng tin tour v hnh nh:", error);
      }
    };

    fetchTour();
  }, [tourID]);
  const formatCurrency = (price) => {
    // Chuyn i gi tr DECIMAL(10) sang s nguyn
    const priceInteger = Math.round(price * 100); // Gi s 2 ch s thp phn

    // S dng hm toLocaleString  nh dng gi theo kiu VN
    const formattedPrice = (priceInteger / 100).toLocaleString("vi-VN", {
      currency: "VND",
    });

    // Thm ch "VN" vo cui chui
    return formattedPrice + " VN";
  };
  const [user, setUser] = useState({
    fullname: "",
    username: "",
    email: "",
    phone: "",
    address: "",
  });

  useEffect(() => {
    const fetchUser = async () => {
      try {
        const response = await axios.get(
          `https://backend-do-an-chuyen-nganh.vercel.app/v1/api/admin/lay-thong-tin-user/${userId}`
        );
        setUser(response.data);
      } catch (error) {
        console.error("Li khi ly thng tin ngi dng:", error);
      }
    };

    fetchUser();
  }, [userID]);
  const handleInputChange = (e) => {
    setUser({
      ...user,
      [e.target.name]: e.target.value,
    });
  };

  const handleSubmit = (e) => {
    e.preventDefault();

    // Gi yu cu cp nht thng tin ngi dng ln server
    axios
      .put(
        `https://backend-do-an-chuyen-nganh.vercel.app/v1/api/admin/cap-nhat-user/${userId}`,
        user
      )
      .then(() => {
        // Chuyn hng v trang danh sch ngi dng sau khi cp nht thnh cng
        window.alert("Cp nht thnh cng!");

        // Chuyn hng n trang login sau 3 giy
      })
      .catch((error) => {
        console.error("Li khi cp nht ngi dng:", error);
      });
  };

  return (
    <>
      <BackToTop />
      <Header />
      <div className="boxGioiThieu"></div>
      <Container>
        <Row className="mt-5">
          <Col className="col-md-4 col-12">
            {/* <img
              src={pic2}
              className="w-100 detailimage"
              style={{ objectFit: "cover" }}
            /> */}
            {images.length > 0 && (
              <img
                src={`data:image/jpeg;base64,${images[0]}`}
                alt="First Image"
                className="w-100 detailimage"
                style={{ objectFit: "cover" }}
              />
            )}
          </Col>
          <Col className="mx-md-4 col-md col-12">
            {" "}
            <Table bordered hover>
              <thead>
                <tr>
                  <th className="text-center fs-3">{tour.name}</th>
                </tr>
              </thead>
              <tbody>
                <tr>
                  <td>
                    <span className="fw-bold">M tour:</span> {tour.id}
                    <br />
                    <span className="fw-bold">Thi gian :</span>{" "}
                    <span className="text-primary fw-bold">
                      {new Date(tour.start_date).toLocaleDateString()} -{" "}
                      {new Date(tour.end_date).toLocaleDateString()}
                    </span>
                    <br /> <span className="fw-bold">Gi:</span>{" "}
                    <span className="text-danger fw-bold">
                      {formatCurrency(tour.price)}{" "}
                    </span>
                    <br />
                    <span className="fw-bold">Ngy khi hnh: </span>{" "}
                    {new Date(tour.start_date).toLocaleDateString()} <br />{" "}
                    <span className="fw-bold">Ni khi hnh:</span>H Ch Minh{" "}
                    <br />
                    <span className="fw-bold">Phng tin :</span>  t
                    <br />
                    <span className="fw-bold">S ch cn :</span>{" "}
                    {tour.quantity}
                    <br />
                  </td>
                </tr>
              </tbody>
            </Table>
          </Col>
        </Row>
        <p className="text-danger fw-light my-3">
          Cc khon ph pht sinh (nu c) nh: ph thu dnh cho khch nc
          ngoi, vit kiu; ph thu phng n; ph thu chnh lch gi tour Nhn
          vin Du Lch Vit s gi in thoi t vn cho qu khch ngay sau khi
          c phiu xc nhn booking. (Trong gi hnh chnh) Trng hp qu khch
          khng ng  cc khon pht sinh, phiu xc nhn booking ca qu khch
          s khng c hiu lc.
        </p>

        <div className=" pt-md-5 mb-md-5 my-4">
          <Row className="d-flex">
            <Col />
            <Col className="col-10 col-md-8">
              <h1 className="text-center  ">
                <LocalOfferIcon fontSize="large" /> BNG GI TOUR CHI TIT
              </h1>
            </Col>
            <Col />
          </Row>
        </div>
        <div>
          <Table striped bordered hover>
            <thead>
              <tr>
                <th>Loi gi\ tui </th>
                <th>Ngi ln(Trn 11 tui) </th>
                <th>Tr em(4 - 11 tui) </th>
                <th>Tr nh({"<"} 4 tui) </th>
              </tr>
            </thead>
            <tbody>
              <tr>
                <td>Gi</td>
                <td className="text-danger fw-bold">
                  {" "}
                  {formatCurrency(tour.price)}
                </td>
                <td className="text-danger fw-bold">
                  {" "}
                  {formatCurrency(tour.child_price)}
                </td>
                <td className="text-danger fw-bold">
                  {" "}
                  {formatCurrency(tour.infant_price)}
                </td>
              </tr>
            </tbody>
          </Table>
        </div>
        <div className=" pt-md-5 mb-md-5 my-4">
          <Row className="d-flex">
            <Col />
            <Col className="col-10 col-md-8">
              <h1 className="text-center  ">
                <InfoIcon fontSize="large" /> THNG TIN LIN H
              </h1>
            </Col>
            <Col />
          </Row>
        </div>
        <div>
          {" "}
          <Form onSubmit={handleSubmit}>
            <Row className="d-flex flex-row">
              <Col className="mx-md-2 col-12 col-md">
                {" "}
                <Form.Group className="mb-3">
                  <Form.Label>
                    H tn : <span className="text-danger">*</span>
                  </Form.Label>
                  <Form.Control
                    type="text"
                    name="fullname"
                    placeholder="H tn"
                    value={user.fullname}
                    onChange={handleInputChange}
                  />
                </Form.Group>
              </Col>
              <Col className="mx-md-2 col-12 col-md">
                {" "}
                <Form.Group className="mb-3" controlId="formBasicPassword">
                  <Form.Label>
                    Email : <span className="text-danger">*</span>
                  </Form.Label>
                  <Form.Control
                    type="email"
                    name="email"
                    placeholder="Email"
                    required
                    value={user.email}
                    onChange={handleInputChange}
                  />
                  <Form.Control.Feedback type="invalid">
                    a ch email khng hp l.
                  </Form.Control.Feedback>
                </Form.Group>
              </Col>
              <Col className="mx-md-2 col-12 col-md">
                {" "}
                <Form.Group className="mb-3" controlId="formBasicPassword">
                  <Form.Label>
                    S in thoi: <span className="text-danger">*</span>
                  </Form.Label>
                  <Form.Control
                    type="number"
                    name="phone"
                    placeholder="S in thoi"
                    required
                    value={user.phone}
                    onChange={handleInputChange}
                  />
                </Form.Group>
              </Col>
            </Row>{" "}
            <Row className="d-flex flex-row">
              <Col className="mx-md-2 col-12 col-md">
                {" "}
                <Form.Group className="mb-3" controlId="formBasicEmail">
                  <Form.Label>
                    a ch : <span className="text-danger">*</span>
                  </Form.Label>
                  <Form.Control
                    type="text"
                    name="address"
                    placeholder="a ch"
                    required
                    value={user.address}
                    onChange={handleInputChange}
                  />
                </Form.Group>
              </Col>
            </Row>{" "}
            <Row>
              <Col className="col-10"></Col>
              <Col>
                {" "}
                <Button
                  variant="dark"
                  type="submit"
                  className="col-md-12 col-12  mt-4 p-2 mb-5"
                >
                  Cp nht li thng tin
                </Button>
              </Col>
            </Row>
          </Form>
          <BookingForm />
        </div>
      </Container>
    </>
  );
};
export default Bookingtour;
